<script>
import cell from '../components/cell'
import { ArrayToObj } from '../utils/utils.js'
import { fetchHeader, callBackNative } from '../api/axios'
import { brigeNativeByJavascript } from '../api/Bridge.js'
import { mapState } from 'vuex'
export default {
    data() {
        return {
            form: {
                userRelationFirst: '',
                relationNameFirst: '',
                relationMobileFirst: '',
                userRelationSecond: '',
                relationNameSecond: '',
                relationMobileSecond: ''
            },
            socials1: [
                { value: 1, text: '父亲' },
                { value: 2, text: '母亲' },
                { value: 3, text: '配偶' },
                { value: 104, text: '兄弟姐妹' },
                { value: 5, text: '同事' }
            ],
            socials2: [
                { value: 1, text: '父亲' },
                { value: 2, text: '母亲' },
                { value: 3, text: '配偶' },
                { value: 104, text: '兄弟姐妹' },
                { value: 5, text: '同事' },
                { value: 7, text: '同学' },
                { value: 4, text: '亲戚' },
                { value: 6, text: '朋友' }
            ],
            hasGetAdress: false
        }
    },
    computed: {
        ...mapState(['appConfig', 'user'])
    },
    methods: {
        ArrayToObj,
        showPicker(title, list, label) {
            this.picker = this.$createPicker({
                title,
                data: [this[list]],
                onSelect: (selectedVal, selectedIndex, selectedText) => {
                    this.form[label] = selectedVal[0]
                },
                onCancel: () => {}
            }).show()
        },
        // 打开通讯录 选择联系人
        async openAdress(num) {
            const data = await callBackNative('jumpAddressBook')
            let { phone, name } = JSON.parse(data)
            phone = phone
                .replace(/\s+/g, '')
                .replace(/-/g, '')
                .replace(/\+86/g, '')
            this.form['relationMobile' + num] = phone
            this.form['relationName' + num] = name
        },
        async submit() {
            if (Object.values(this.form).some(x => x === '')) {
                this.$store.commit('setMsg', '请填写完整')
                return
            }
            let form = { ...this.form, ...this.$route.params.form }
            this.$store.commit('loading', true)
            setTimeout(() => {
                let certification = this.user.certification
                certification.person = true
                this.$store.commit('setUser', { certification })
                this.$store.commit('setMsg', '认证成功')
                this.$store.commit('loading', false)
                this.$router.go(-2)
            }, 2000)
            // const data = await fetchHeader('/auth/authUserInfo', form, 'post')
            // if (data.code === 0) {
            //     this.$store.commit('setMsg', '提交成功')
            //     if (!this.hasGetAdress) {
            //         // 上传所有通讯录
            //         brigeNativeByJavascript('getAddressBook')
            //         this.hasGetAdress = true
            //     }
            //     // 关闭 webview
            //     brigeNativeByJavascript('closeApplication')
            // }
        }
    },
    beforeCreate() {
        document.title = '联系人信息'
    },
    components: {
        cell
    }
}
</script>
